<template>
  <div class="sidebar">
    <el-col>
      <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" theme="dark" router>
        <template v-for="item in menu">
          <el-menu-item :index="item.href"><span :class="item.icon"></span>{{item.msg}}</el-menu-item>
        </template>
        <li class="el-menu-item" @click="exit()"><span class="icon-exit"></span>安全退出</li>
      </el-menu>
    </el-col>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        menu: [
          {
            index: 1,
            icon: '',
            msg: '首页',
            href: 'index'
          },
          {
            index: 2,
            icon: '',
            msg: '广告管理',
            href: 'adManage'
          },
          {
            index: 3,
            icon: '',
            msg: '充值记录',
            href: 'moneyRecord'
          },
          {
            index: 4,
            icon: '',
            msg: '资料修改',
            href: 'alterInfo'
          }
        ]
      }
    },
    computed: {
      onRoutes(){
        return this.$route.path.replace('/', '')
      }
    },
    methods: {
      exit(){
        this.$router.push({path:'/login'})
        window.localStorage.removeItem('nGGname')
        window.localStorage.removeItem('nGGtoken')
        window.localStorage.removeItem('nGGuser_id')
        this.$message({
          message: '退出登录成功！',
          duration: 1500
        })

      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .sidebar
    display: block
    position: absolute
    top: 80px
    left: 0
    bottom: 0
    width: 200px;
    overflow: auto;
    background: #324157;
    .el-menu-item
      text-align: center;
      span
        margin: 10px
</style>



