<template>
  <div class="header">
    <div class="icon"></div><div class="site_name">广告主</div>
    <div class="userName">
      <span>欢迎您：</span><span>{{userName}}</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    computed:{
      userName() {
        return window.localStorage.getItem('nGGname')
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .header
    width: 100%;
    height: 80px;
    box-sizing:border-box
    padding:0 50px
    overflow: hidden;
    border-bottom: 1px solid #D6D7D9;
    div
      float: left
      line-height: 80px
    .site_name
      padding:0 15px
    .icon
      width: 60px
      height: 60px
      margin-top: 10px
      background: url(../image/Baikal_Logox.png) no-repeat
      background-size: 100%
    .userName
      float: right
      font-size: $font-size-medium
</style>



